#checkboxesContainer {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	margin-bottom: 15px;
	-ms-flex-pack: start;
	    justify-content: flex-start;
}

#pharmacyCheckboxesTitle {
	-ms-flex-preferred-size: 100%;
	    flex-basis: 100%;
	font-size: 1.15em;
}

.checkbox {
	margin-right: 30px;
 }
 
 input[type=checkbox] {
	 -webkit-transform: scale(1.5);
	     -ms-transform: scale(1.5);
	         transform: scale(1.5);
	 margin-right: 10px;
 }

 
@media screen and (max-width: 1024px) {
	.checkbox {
		-ms-flex-preferred-size: 15%;
		    flex-basis: 15%;
		margin-right: 0px;
		margin-top: 10px;
	}
}

@media screen and (max-width: 780px) {
	.checkbox {
		-ms-flex-preferred-size: 20%;
		    flex-basis: 20%;
	}
}

@media screen and (max-width: 590px) {
	.checkbox {
		-ms-flex-preferred-size: 25%;
		    flex-basis: 25%;
	}
}

@media screen and (max-width: 480px) {
	.checkbox {
		-ms-flex-preferred-size: 50%;
		    flex-basis: 50%;
	}
}


