#seacrhContainer {
	-ms-flex: 1 1;
	    flex: 1 1;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-ms-flex-pack: left;
	    justify-content: left;
}

#searchBarInput {
	-ms-flex-preferred-size: 45%;
	    flex-basis: 45%;
	margin-right: 30px;
}

.searchBarButton {
	-ms-flex-preferred-size: 18%;
	    flex-basis: 18%;
	margin-right: 30px;
}

#mobileView {
	display: none;
}

#userLatLng {
	-ms-flex-preferred-size: 100%;
	    flex-basis: 100%;
}

#underline {
	text-decoration: underline;
}

@media screen and (max-width: 600px) {
	#seacrhContainer {
		-ms-flex-pack: left;
		    justify-content: left;
	}
	
	#searchBarInput {
		-ms-flex-preferred-size: 100%;
		    flex-basis: 100%;
		margin-right: 0px;
		line-height: 35px; 
	}

	.searchBarButton {
		-ms-flex-preferred-size: 40%;
		    flex-basis: 40%;
		margin-right: 15px;
		margin-top: 10px;
	}

	#mobileView {
		display: inline-block;
	}

	#tabletDesktopView {
		display: none;
	}

	#userLatLng {
		font-size: 0.9em;
	}
}

@media screen and (max-width: 375px) {
	#userLatLng {
		font-size: 0.81em;
	}
}

@media screen and (max-width: 330px) {
	#userLatLng {
		font-size: 0.72em;
	}
}



