#navBarContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
  -ms-flex-align: center;
      align-items: center;
}

#navBarSearchBar, #navBarSearchButton {  
  line-height: 35px; 
}

#navBarSearchBar {
  width: 500px;
}

#navBarLinks a {
  text-decoration: none;
  margin: 10px;
  color: black;
 }

 #mobileFlexContainer {
   display: none;
 }

 @media screen and (max-width: 1100px) {
	#navBarSearchBar {
    width: 350px;
  }
}

@media screen and (max-width: 820px) {
	#navBarSearchBar {
    width: 170px;
  }
}

@media screen and (max-width: 640px) {
  #navBarContainer {
    display: none;
  }

  #mobileFlexContainer {
    position: relative;
    padding: 0;
    margin: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
  }

  .mobileFlexItem {
    display: -ms-flexbox;
    display: flex;
    padding: 5px;
  }

  #mobileFlexContainer i {
    position: absolute;
    right: 0;
  }
}


